.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
  color: #fff;
}

.btn--medium {
  padding: 10px 20px;
  font-size: 18px;
}
  
.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}
 
.btn--back {
  position: fixed;
  top: 15px;
  left: 15px;
  background-color: #AA336A;
  border-radius: 20px;
  z-index: 1000;
  transition: all 0.3s ease-out;
  &:hover {
    transition: all 0.3s ease-out;
    background: #770737;
    color: #fff;
    transition: 250ms;
  }
}

.btn--reset {
  margin-top: 40px;
  background-color: #faa0a0;
  color: #242424;
  border-radius: 20px;
  &:hover {
    transition: all 0.3s ease-out;
    background: #770737;
    color: #fff;
    transition: 250ms;
  }
}

/* Yes button specific styling */
.btn--yes {
  background-color: #30c51a;
  padding: 5px 10px;
  border-radius: 5px;
  &:hover {
    transition: all 0.3s ease-out;
    background-color: #2ab415;
  }
}

/* No button specific styling */
.btn--no {
  background-color: #e62c2c;
  padding: 6px 10px;
  margin-left: 10px;
  border-radius: 5px;
  &:hover {
    transition: all 0.3s ease-out;
    background-color: #2ab415;
  }
}