.cards {
    padding: 4rem;
    background: #FFD1DC;
  }
  
  h1 {
    text-align: center;
  }
  
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
  }
  
  .cards__wrapper {
    position: relative;
  }
  
  .cards__items {
    margin-bottom: 55px;
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
}
  
.cards__item__link {
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(23, 15, 23, 0.17);
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
}
  
  .cards__item__pic-wrap {
    position: relative;
    margin: 0;
    padding-top: 60%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__img {
    position: absolute;
    bottom: 0;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 0px 30px 30px;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }