.valentine-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 45px;
}

.yesButton {
  padding: 5px 10px;
  border-radius: 5px;
  border: none;
  background-color: #30c51a;
  color: white;
  cursor: pointer;
  &:active {
    background-color: #2ab415;
  }
  &:focus {
    outline: none;
  }
  &:hover {
    background-color: #2ab415;
  }
}

.noButton {
  padding: 6px 10px;
  margin-left: 10px;
  border-radius: 5px;
  border: none;
  background-color: #e62c2c;
  color: white;
  font-size: 25px;
  cursor: pointer;
  &:active {
    background-color: #c72727;
  }
  &:focus {
    outline: none;
  }
  &:hover {
    background-color: #c72727;
  }
}

.text{
  margin-top: 25px;
  margin-bottom: 10px;
}